.Wrapper {
    width: 91%;
    height: calc(100vh);
    position: -webkit-sticky;
    position: fixed;
    margin-left: auto;
    margin-right: auto;
    left: 0; right: 0;
    top: 0px;
}
.Wrapper_image {
    width: 100%;
    height: calc(100% - 88px);
    background-image: url(../../media/mountains_wrapper.png);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    /* object-fit: cover; */
    /* border-radius: 40px; */
    margin-top: 88px;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
}
.Wrapper_block_GEOTEC {
    width: 50%;
    height: auto;
    position: absolute;
    top: 157px;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: flex-start;
}
.Wrapper_block_GEOTEC_block1 {
    height: auto;
    width: auto;
    position: relative;
    left: 74px;
    top: 0;
    /* top: 75px; */
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    transition: 2s ease;
}
.Wrapper_block_GEOTEC_block2 {
    height: auto;
    width: auto;
    position: relative;
    top: -33px;
    /* top: 42px; */
    left: 74px;
    color: #3856B5;
    font-family: 'Raleway-Regular';
    font-size: 24px;
    transition: 2s ease;
}
.Wrapper_block_GEOTEC_block3 {
    height: auto;
    width: auto;
    position: relative;
    top: 77px;
    left: 74px;
    color: #3856B5;
    font-family: 'Raleway-Regular';
    font-size: 24px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: 2s ease;
}
.Wrapper_block_GEOTEC_block2_button {
    cursor: pointer;
    position: relative;
    left: 49px;
}
.Wrapper_block_GEOTEC_block2_title {
    font-family: 'Poppins-Light';
    font-size: 24px;
} 
.Wrapper_block_GEOTEC_title {
    width: 85%;
    text-transform: uppercase;
    font-size: 164px;
    font-family: 'Poppins-ExtraLight';
    color: #3856B5;
    line-height: 246px;
    font-weight: 275;
    font-style: normal;
}
.Wrapper_block_GEOTEC_logo {
    width: 15%;
    position: relative;
    left: 56px;
}
.Wrapper_block_GEOTEC_logo svg {
    position: relative;
    left: 0px;
    /* filter: url(#filter); */
}

.Wrapper_block_GEOTEC_logo path {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.backgroundSvgLogo {
    position: relative;
    /* left: -4px;
    top: 102px; */
    width: 190%;
    left: -45%;
    height: 190%;
    bottom: -45%;
    /* fill: url(#paint0_radial_1401_3081); */
    transform-origin: center center;
    /* background: radial-gradient(70.59% 70.59% at 29.41% 50%, #3ED4A9 0%, #4762B9 100%); */
    animation: logoPath_recGradientRotate 5s linear infinite;
}

.recGradientRotate {
    animation: logoPath_recGradientRotate 20s linear infinite;
}

