@keyframes logoPath_recGradientRotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes logoPath1 {
    0% {
        transform: translateY(10px);
    }
    50% {
        transform: translateY(50px);
    }
    100% {
        transform: translateY(10px);
    }
}